@import './colors.scss';
.card{
    font-family: sans-serif;
      position: relative;

      overflow: hidden;
      transition: $transition;
      z-index: 2;
      overflow: hidden;
      box-shadow: 0px 0px 6px $shadow;
      position: relative;
      & img{
          height: 100%;
          width: 100%;
      }
      & a{
        background-color: none;
        
        font-family: serif;
      }
      & .card-url{
          position: absolute;
          left: 120%;
          top: 35%;
          border-radius: 4px;
          width: 100%;
          cursor: pointer;
          color: color(white);
          background-color: color(gray);
          font-size: 2.4rem;
          transition: $transition;
          padding: 0.4rem;
          
          
          &:hover{
              color: color(primary, base);
              
              border: 1px solid color(primary,base);
          }
          & .card-git-url{
            font-size: 2rem;

            &:hover .bxs-git{
              color: color(primary,base);

            }
            & .bxs-git{
              font-size: 3rem;
            }
          }

          & > a{
            font-size: 2.5rem;
            color: color(white);
            &:hover{
              color: color(primary,base);

            }
            
          }
      }


        & .card-btn{
          width: 100%;
          height: 2.8rem;
          position: absolute;
          top: 40%;
          right: -100%;
            display:flex;
            background-color: color(primary,base);
            transition: all 0.5s ease;
            justify-content: center;
            align-items: center;
            color: color(white);
            text-transform: capitalize;
            border: none;
            border-radius: 5px;
            z-index: 1;
            &:hover{
              background-color: color(white);
              color:  color(primary,base);
              border: 1px solid color(primary,base);
            }
          }
      

      
      

      & .title{
        font-family: Arial, Helvetica, sans-serif;
         // position: absolute;
          top: -10%;
          left: 0%;
       //   transform: translate(-50%, 50%);
        //  transform: translateY(-8rem);
          color: $white;
          font-size: 1rem;
          font-weight: bold;
          background-color: color(primary,base);
          width: 100%;
          transition: $transition;
          padding: $padding__default;


      }

    &:hover .card-btn{
        right: -10%;

    }
  }