@import './colors.scss';
.loader{
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,50%);
    margin: 0;
    padding: 0;

    &  li{
        width: 40px;
        height: 40px;
        border-radius: 50%;
        list-style: none;
        margin: 0 0.4rem;
        animation: animate 1.6s ease-in-out infinite;
    }
    @keyframes animate {
        0%,40%,100%{
            transform: scale(0.3);
        }
        20%{
            transform: scale(1);
        }
    }

}
.loader li:nth-child(1){
    animation-delay: -1.4s;
    box-shadow: 0 0 50px color(primary,base);
}
.loader li:nth-child(2){
    animation-delay: -1s;
    box-shadow: 0 0 50px color(primary,base);
}
.loader li:nth-child(3){
    animation-delay: -0.8s;
    box-shadow: 0 0 50px color(primary,base);
}
.loader li:nth-child(4){
    animation-delay: -0.4s;
    box-shadow: 0 0 50px color(primary,base);
}
